<template>
  <div class="block">
    <div class="body">
      <div class="list cl">
        <p class="left fl">contractRet</p>
        <p class="right fl">
          {{ orderid }}
        </p>
      </div>
      <div class="list cl">
        <p class="left fl">Transaction hash</p>
        <p class="right fl">
          {{ hash }}
        </p>
      </div>
      <div class="list cl">
        <p class="left fl">Block height</p>
        <p class="right fl">
          {{ blockNumber }}
        </p>
      </div>
      <div class="list cl">
        <p class="left fl">from</p>
        <p class="right fl color1" @click="address(from)">
          {{ from }}
        </p>
      </div>
      <div class="list cl">
        <p class="left fl">to</p>
        <p class="right fl color1" @click="address(to)">
          {{ to }}
        </p>
      </div>
      <!-- <div class="list cl">
        <p class="left fl">contract</p>
        <p class="right fl">
          {{ contract=='Trx'? 'ADS' : contract}}
        </p>
      </div> -->
      <div class="list cl">
        <p class="left fl">value</p>
        <p class="right fl color2">
          {{ value }}
          {{ contract==1? 'USDT' : 'ADS'}}
        </p>
      </div>
      <div class="list cl">
        <p class="left fl">Trading hours</p>
        <p class="right fl">
          {{ time }}
        </p>
      </div>
    </div>
    <!-- 从到 -->
    <div class="boxs cl">
      <div class="fl le">
        <h4>from</h4>
        <div class="cl jia">
          <p class="fl" style="color: #00eeff" @click="address(from)">
            {{ from }}
          </p>
          <p class="fl" style="color: #ff2549">-{{ value }}ADS</p>
        </div>
      </div>
      <div class="cen fl">
        <img src="../../static/image/cen.png" alt="" />
      </div>
      <div class="fr ri">
        <h4>to</h4>
        <div class="cl jia">
          <p class="fl" style="color: #00eeff; margin-left: 10%" @click="address(to)">
            {{ to }}
          </p>
          <p class="fl" style="color: #0fe838">+{{ value }}ADS</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../components/Header";
export default {
  components: {
    "app-header": Header,
  },
  name: "App",
  methods: {
    handleSizeChange(val) {
      //   console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      //   console.log(`当前页: ${val}`);
    },
  },
  data() {
    return {
      orderid: "",
      hash: "",
      blocknumber: "",
      from: "",
      to: "",
      contract: "",
      value: "",
      m_number: "",
      time: "",
    };
  },
  methods: {
    has: function () {
      this.$api.get(
        "/hashInfo",
        {},
        { hash: this.$route.query.ha },
        (ret) => {
            console.log(ret);
            this.orderid ='SUCCESS';
            this.hash = ret.info.hash;
            this.blockNumber = ret.info.block_num;
            this.from = ret.info.from_address;
            this.to = ret.info.to_address;
            this.contract = ret.info.currency;
            this.value = ret.info.amount;
            this.time = ret.info.created_at;
        }
      );
    },

    // 地址
    address: function (address) {
      this.$router.push({
        path: "/AddressDetails",
        name: "AddressDetails",
        //query传参 需要使用 name 否则取不到；对应路由配置的 name
        query: {
          address: address,
        },
      });
    },
  },
  created() {
    // console.log(this.$route.query.ha);
    this.has();
  },
  filters: {
    formatDate: function(value) {
      let date = new Date(Number(value));
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? '0' + MM : MM;
      let d = date.getDate();
      d = d < 10 ? '0' + d : d;
      let h = date.getHours();
      h = h < 10 ? '0' + h : h;
      let m = date.getMinutes();
      m = m < 10 ? '0' + m : m;
      let s = date.getSeconds();
      s = s < 10 ? '0' + s : s;
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    }
  },
   watch:{
      '$route'(to,from){
        this.has();
        console.log(this.$route.query.block)
      }
    },
};
</script>

<style  scoped>
.block {
  width: 80%;
  height: 100%;
  margin: 0 auto;
  padding: 80px 0;
  box-sizing: border-box;
}
.herder {
  width: 100%;
  height: 80px;
  background: rgba(255, 255, 255, 0.02);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 998;
}
.boxs {
  margin: 80px auto 40px;
  padding-bottom: 74px;
}
.body {
  background: url("../../static/image/block.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 8px;
  margin: 80px auto 40px;
  padding-bottom: 74px;
  box-sizing: border-box;
}
.body p {
  height: 60px;
  line-height: 60px;
  cursor: pointer;
  /* text-align: center; */
  font-size: 13px;
}
.body p:nth-child(1) {
  width: 30%;
}
.body p:nth-child(2) {
  width: 68%;
  text-align: left;
}
.le {
  background: url("../../static/image/li.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 45%;
  height: 240px;
  box-sizing: border-box;
  padding: 20px;
}
.cen {
  width: 10%;
}
.cen img {
  margin-top: 60px;
}
.ri {
  background: url("../../static/image/li2.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 45%;
  height: 240px;
  box-sizing: border-box;
  padding: 20px;
}
h4 {
  text-align: center;
}
.jia {
  font-size: 14px;
  margin-top: 50px;
}
.jia p:nth-child(1) {
  width: 40%;
  margin-right: 10%;
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden;
}
.kong {
  margin-top: 80px;
}
.left {
  box-sizing: border-box;
  padding-left: 10%;
}
.color1 {
  color: #00eeff;
}
.color2 {
  color: #0fe838;
}
</style>



