<template>
  <div class="block">
    <div class="body">
      <h3 class="Wcolor">Block record</h3>
      <div class="biaoti">
        <p class="van-ellipsis">Current block height</p>
        <p class="van-ellipsis">blockHash</p>
        <p class="van-ellipsis">witnessAddress</p>
        <p class="van-ellipsis">parentHash</p>
        <p class="van-ellipsis">transactionsNum</p>
        <p class="van-ellipsis">Generation time</p>
      </div>
      <div class="biao2" v-for="(val, index) in list" :key="index">
        <p class="van-ellipsis" @click="block(val.blockNumber)">{{ val.blockNumber }}</p>
        <p  class="van-ellipsis" @click="block(val.blockNumber)">{{ val.blockHash }}</p>
        <p  class="van-ellipsis" @click="block(val.blockNumber)">{{ val.witnessAddress }}</p>
        <p class="van-ellipsis" @click="block(val.blockNumber)">{{ val.parentHash }}</p>
        <p class="van-ellipsis" @click="block(val.blockNumber)">{{ val.now_num }}</p>
        <p class="van-ellipsis" @click="block(val.blockNumber)">{{ val.blockTimestamp |formatDate}}</p>
      </div>
    </div>
    <div class="blok">
      <el-pagination
        @prev-click="shang"
        @next-click="xia"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage1"
        :page-size="1"
        :total="pages"
        layout="total, prev, pager, next"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import Header from "../components/Header";
export default {
  components: {
    "app-header": Header,
  },
  name: "App",
  methods: {
    handleSizeChange(val) {
      //   console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      //   console.log(val);
      localStorage.setItem("key", val);
      this.blocks(val);
    },
    xia(val) {
      console.log(val);
      localStorage.setItem("key", val);
      this.blocks(val);
    },
    shang(val) {
      //   console.log(val);
      localStorage.setItem("key", val);
      this.blocks(val);
    },
    block: function (block) {
      this.$router.push({
        path: "/New",
        name: "New",
        //query传参 需要使用 name 否则取不到；对应路由配置的 name
        query: {
          block: block,
        },
      });
    },
    blocks: function (val) {
      console.log(val)
      console.log(localStorage.getItem("key"))
      if (val) {
        var page = val;
      } else if (localStorage.getItem("key")) {
        var page = localStorage.getItem("key");
      } else {
        var page = 1;
      }
      this.$api.get(
        "/blockList",
        {},
        { page: page },
        (ret) => {
            console.log(ret);
            this.pages = ret.last_page;
            this.list = ret.data;
            document.getElementsByClassName(
              "el-pagination__total"
            )[0].innerHTML = `共  ${this.pages}  页`;
        }
      );
      //   console.log(this.$api)
    },
    current_page: function () {
      if (localStorage.getItem("key")) {
        var pages = localStorage.getItem("key");
        this.currentPage1 = Number(pages);
      }
    },
  },
  data() {
    return {
      list: [],
      currentPage1: 1,
      pages: 1,
    };
  },
  created() {
    this.blocks();
    // console.log(localStorage.getItem("key"));
    this.current_page();
  },
  filters: {
    formatDate: function(value) {
      let date = new Date(Number(value));
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? '0' + MM : MM;
      let d = date.getDate();
      d = d < 10 ? '0' + d : d;
      let h = date.getHours();
      h = h < 10 ? '0' + h : h;
      let m = date.getMinutes();
      m = m < 10 ? '0' + m : m;
      let s = date.getSeconds();
      s = s < 10 ? '0' + s : s;
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    }
  }
};
</script>

<style  scoped>
.block {
  width: 80%;
  height: 100%;
  margin: 0 auto;
  padding: 80px 0;
  box-sizing: border-box;
}
.herder {
  width: 100%;
  height: 80px;
  background: rgba(255, 255, 255, 0.02);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 998;
}
.body {
  background: url("../../static/image/block.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 8px;
  margin: 80px auto 40px;
  padding-bottom: 74px;
  box-sizing: border-box;
}
.Wcolor {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
}
.biaoti {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  height: 60px;
  width: 90%;
  padding: 0 10px;
  margin: 0 auto;
  background: url("../../static/image/block2.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.biaoti p:nth-child(2) {
  width: 22%;
  text-align: center;
  line-height: 60px;
}
.biaoti p:nth-child(1) {
  width: 10%;
  text-align: left;
  line-height: 60px;
}
.biaoti p:nth-child(3) {
  width: 22%;
  text-align: center;
  line-height: 60px;
}
.biaoti p:nth-child(4) {
  width: 22%;
  text-align: center;
  line-height: 60px;
}
.biaoti p:nth-child(5) {
  width: 10%;
  text-align: center;
  line-height: 60px;
}
.biaoti p:nth-child(6) {
  width: 14%;
  text-align: center;
  line-height: 60px;
}
.biao2 {
  cursor: pointer;
}
.biao2 p:nth-child(2) {
  width: 22%;
  text-align: center;
  line-height: 60px;
}
.biao2 p:nth-child(1) {
  width: 10%;
  text-align: left;
  line-height: 60px;
  color: #00eeff;
}
.biao2 p:nth-child(3) {
  width:22%;
  text-align: center;
  line-height: 60px;
}
.biao2 p:nth-child(4) {
  width:22%;
  text-align: center;
  line-height: 60px;
}
.biao2 p:nth-child(5) {
  width: 10%;
  text-align: center;
  line-height: 60px;
}
.biao2 p:nth-child(6) {
  width:14%;
  text-align: right;
  line-height: 60px;
}
.biao2 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  height: 60px;
  width: 90%;
  margin: 0 auto;
}
</style>



