<template>
  <div class="block">
    <!-- logo -->
    <div class="body">
      <div class="adress cl">
        <p class="addresss fl van-ellipsis" data-clipboard-text="address">
          Trading address:{{ address }}
        </p>
        <div class="img fl">
          <img
            src="../../static/image/fuzhi.png"
            alt=""
            v-clipboard:copy="address"
            v-clipboard:success="onCopy"
          />
        </div>
      </div>
      <div class="boxs cl">
        <div class="fll">
          <div class="images fl">
            <img src="../../static/image/le1.png" alt="" />
          </div>
          <div class="ri fl">
            <p class="top1">{{ esc }}ADS</p>
            <!-- <p class="top2">≈{{ escusdt }}USDT</p> -->
          </div>
        </div>
        <div class="flr">
          <div class="images fl">
            <img src="../../static/image/le1.png" alt="" />
          </div>
          <div class="ri fl">
            <!-- <p class="top1">{{ esc }}ADS</p> -->
            <p class="top1">{{ escusdt }}USDT</p>
          </div>
        </div>
      </div>
    </div>
    <div class="body1">
      <div class="jilu">
        <p class="fl lu">Transaction records</p>
        '<!-- <div class="fr">
          <el-dropdown @command="handleCommand">
            <el-button type="primary">
              {{ command }}<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
           <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="DDT">DDT</el-dropdown-item>
              <el-dropdown-item command="DDC">DDC</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div> -->'
      </div>
      <div class="biaoti">
        <p class="van-ellipsis">block Number</p>
        <p class="van-ellipsis">hash</p>
        <p class="van-ellipsis">from</p>
        <p class="van-ellipsis">to</p>
        <p class="van-ellipsis">value</p>
        <p class="van-ellipsis">time</p>
      </div>
      <div class="biao2 cl" v-for="(val, index) in order" :key="index">
        <p class="naddress van-ellipsis ">{{ val.block_num }}</p>
        <p class="Number van-ellipsis colors" @click="ha(val.hash)">{{ val.hash }}</p>
        <p class="times van-ellipsis colors" @click="addressrr(val.from_address)">{{ val.from_address }}</p>
        <p class="times van-ellipsis colors"  @click="addressrr(val.to_address)">{{ val.to_address }}</p>
        <p class="address van-ellipsis ">{{ val.amount }}</p>
        <p class="taddress van-ellipsis">{{ val.created_at}}</p>
      </div>
      <div class="blok">
        <el-pagination
          @prev-click="shang"
          @next-click="xia"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage1"
          :page-size="1"
          :total="pages"
          layout="total, prev, pager, next"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../components/Header";
import Clipboard from "clipboard";
export default {
  components: {
    "app-header": Header,
  },
  name: "Address",
  data() {
    return {
      address: "",
      est: "",
      estusdt: "",
      esc: "",
      command: "DDT",
      escusdt: "",
      order: [],
      currentPage1: 1,
      pages: 1,
    };
  },
  methods: {
    handleSizeChange(val) {
      //   console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      //   console.log(val);
      localStorage.setItem("key", val);
      this.adresss(val);
    },
    xia(val) {
      //   console.log(val);
      localStorage.setItem("key", val);
      this.adresss(val);
    },
    shang(val) {
      //   console.log(val);
      localStorage.setItem("key", val);
      this.adresss(val);
    },
    ha: function (ha) {
      this.$router.push({
        path: "/records",
        name: "records",
        //query传参 需要使用 name 否则取不到；对应路由配置的 name
        query: {
          ha: ha,
        },
      });
    },

    addressrr: function(address) {
      this.$router.push({
        path: '/AddressDetails',
        name: 'AddressDetails',
        //query传参 需要使用 name 否则取不到；对应路由配置的 name
        query: {
          address: address
        }
      });
    },
    adresss(val) {
      if (val) {
        var page = val;
      } else if (localStorage.getItem("key")) {
        var page = localStorage.getItem("key");
      } else {
        var page = 1;
      }
      console.log(this.$route.query.address)
      this.$api.get(
        "/addressInfo",
        {},
        { address: this.$route.query.address ,page: page },
        (ret) => {
            console.log(ret);
            this.address = ret.address;
            this.esc = ret.asset_ads;
            this.escusdt = ret.asset_usdt;
            this.order = ret.tra.data
            this.pages = ret.tra.last_page;
            document.getElementsByClassName(
              "el-pagination__total"
            )[0].innerHTML = `共  ${this.pages}  页`;
        }
      );
    },
    handleCommand(command) {
      this.command = command;
      var val = 1;
      this.julu(val, command);
      localStorage.setItem("key", "");
    },
    onCopy() {
      this.$message({
        message: "复制成功",
        type: "success",
      });
    },
  },
  created() {
    this.adresss();
      console.log(this.$route.query.address)
  },
  filters: {
    formatDate: function(value) {
      let date = new Date(Number(value));
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? '0' + MM : MM;
      let d = date.getDate();
      d = d < 10 ? '0' + d : d;
      let h = date.getHours();
      h = h < 10 ? '0' + h : h;
      let m = date.getMinutes();
      m = m < 10 ? '0' + m : m;
      let s = date.getSeconds();
      s = s < 10 ? '0' + s : s;
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    }
  },
   watch:{
      '$route'(to,from){
          this.adresss();
        console.log(this.$route.query.address)
      }
    },
};
</script>

<style  scoped>
.el-button--primary {
