  left: 0;
  z-index: 998;
}
.body {
  background: url("../../static/image/block.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 8px;
  margin: 80px auto 40px;
  box-sizing: border-box;
  padding: 30px;
}
.body1 {
  background: url("../../static/image/block.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 8px;
  margin: 80px auto 40px;
  box-sizing: border-box;
  padding: 30px;
  padding-top: 20px;
}
.Wcolor {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
}
.left {
  width: 25%;margin: 0 auto;
}
.adress {
  height: 30px;
}
.addresss {
  font-size: 16px;
  line-height: 30px;
  cursor: pointer;
}
.adress img {
  height: 16px;
  width: auto;
  margin-top: 7px;
  margin-left: 10px;
  cursor: pointer;
}
.boxs {
  margin-top: 60px;
      display: flex;
      align-items: center;
      justify-content: space-between;
}
.boxs img {
  height: 90px;
  width: auto;
}
.boxs .fll{
  width: 50%;
}
.boxs .flr{
  width: 50%;
}
.ri {
  margin-left: 10px;
}
.top1 {
  font-size: 16px;
  line-height: 90px;
  cursor: pointer;
  margin-left: 15px;
}
.top2 {
  font-size: 16px;
  line-height: 30px;
  cursor: pointer;
}
.lu {
  padding: 12px 0;
}
.biaoti {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  height: 60px;
  width: 100%;
  margin: 0 auto;
  /* background: url("../../static/image/block2.png"); */
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.biaoti p:nth-child(1) {
  width: 10%;
