}


.biao2 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  height: 60px;
  width: 100%;
  margin: 0 auto;
  font-size: 14px;
}
.naddress {
  width: 10%;
  text-align: left;
  line-height: 60px;
}
.address {
  width: 20%;
  text-align: center;
  line-height: 60px;
}
.Number {
  width: 20%;
  text-align: center;
  line-height: 60px;
}
.Hash {
  width: 10%;
  text-align: left;
  line-height: 60px;
}
.times {
