<template>
  <div class="block">
    <div class="body">
      <div class="list cl">
        <p class="left fl">Block height</p>
        <p class="right fl">{{ blocknumber }}</p>
      </div>
      <div class="list cl">
        <p class="left fl">block kHash</p>
        <p class="right fl">{{ blockHash }}</p>
      </div>
      <div class="list cl">
        <p class="left fl">txTrieRoot</p>
        <p class="rem right fl">{{ txTrieRoot }}</p>
      </div>
      <div class="list cl">
        <p class="left fl">witnessAddress</p>
        <p class="right fl">{{ witnessAddress }}</p>
      </div>
      <div class="list cl">
        <p class="left fl">parentHash</p>
        <p class="right fl">{{ parentHash }}</p>
      </div>
      <div class="list cl">
        <p class="left fl">blockTimestamp</p>
        <p class="right fl">{{ blockTimestamp | formatDate }}</p>
      </div>
      <div class="list cl">
        <p class="left fl">transactionsNum</p>
        <p class="right fl">{{ transactionsNum }}</p>
      </div>
    </div>
    <div class="body2">
      <h3 class="Wcolor">Block record</h3>
      <div class="biaoti">
        <p class="van-ellipsis">block Number</p>
        <p>hash</p>
        <p>from</p>
        <p>to</p>
        <p>value</p>
        <p>time</p>
      </div>
      <div class="biao2" v-for="(val, index) in transaction" :key="index">
        <p class="address van-ellipsis ">{{ val.block_num }}</p>
        <p class="Number van-ellipsis color" @click="ha(val.hash)">{{ val.hash }}</p>
        <p class="times van-ellipsis" @click="addressgo(val.from_address)">{{ val.from_address }}</p>
        <p class="times van-ellipsis" @click="addressgo(val.to_address)">{{ val.to_address }}</p>
        <p class="addressa van-ellipsis ">{{ val.amount }}{{val.currency==1?'USDT':'ADS'}}</p>
        <p class="addressb van-ellipsis">{{ val.created_at  }}</p>
      </div>

      <div class="blok">
        <el-pagination
          @prev-click="shang"
          @next-click="xia"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage1"
          :page-size="1"
          :total="pages"
          layout="total, prev, pager, next"
        >
        </el-pagination>
      </div>
    </div>
    <!-- 从到 -->
  </div>
</template>

<script>
import Header from '../components/Header';
export default {
  components: {
    'app-header': Header
  },
  name: 'New',
  data() {
    return {
      blocknumber: [],
      blockHash: '',
      txTrieRoot: '',
      witnessAddress: '',
      parentHash: '',
      blockTimestamp: '',
      transactionsNum: '',
      transaction: '',
      placeholders: 'Block / address / hash',
      pages:1

    };
  },
  methods: {
    blocks: function(bnum) {
      this.$api.get('/blockInfo', {}, { block: bnum }, ret => {
        console.log(ret);
        this.blocknumber = ret.block.blockNumber;
        this.blockHash = ret.block.blockHash;
        this.txTrieRoot = ret.block.txTrieRoot;
        this.witnessAddress = ret.block.witnessAddress;
        this.parentHash = ret.block.parentHash;
        this.blockTimestamp = ret.block.blockTimestamp;
        this.transactionsNum = ret.block.now_num;
        this.transaction = ret.tra.data;

        this.pages = ret.tra.last_page;
        document.getElementsByClassName(
          "el-pagination__total"
        )[0].innerHTML = "共" + "" + this.pages + "" + "页";
        this.list = ret.data;
      });
    },
    // 哈希
    ha: function(ha) {
      this.$router.push({
        path: '/records',
        name: 'records',
        //query传参 需要使用 name 否则取不到；对应路由配置的 name
        query: {
          ha: ha
        }
      });
    }
  },
  
  // 地址
  addressgo: function (address) {
    this.$router.push({
      path: "/AddressDetails",
      name: "AddressDetails",
      //query传参 需要使用 name 否则取不到；对应路由配置的 name
      query: {
        address: address,
      },
    });
  },
  created() {
    this.blocks(this.$route.query.block);
  },
  watch: {
    $route(to, from) {
      console.log(1);
      console.log(to.query.block);
      console.log(from.query.block);
      this.blocks(this.$route.query.block);
      console.log(this.$route.query.block);
    }
  },
  filters: {
    formatDate: function(value) {
      let date = new Date(Number(value));
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? '0' + MM : MM;
      let d = date.getDate();
      d = d < 10 ? '0' + d : d;
      let h = date.getHours();
      h = h < 10 ? '0' + h : h;
      let m = date.getMinutes();
      m = m < 10 ? '0' + m : m;
      let s = date.getSeconds();
      s = s < 10 ? '0' + s : s;
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    }
  }
};
</script>

<style scoped>
