  width: 30%;
  padding-left: 5%;
}
.body p:nth-child(2) {
  width: 65%;
}
.le {
  background: url('../../static/image/li.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 45%;
  height: 240px;
  box-sizing: border-box;
  padding: 20px;
}
.cen {
  width: 10%;
}
