  text-align: left;
}
.cen img {
  margin-top: 60px;
}
.ri {
  background: url('../../static/image/li2.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 45%;
  height: 240px;
  box-sizing: border-box;
  padding: 20px;
}
h4 {
  text-align: center;
}
.jia {
  font-size: 14px;
  margin-top: 50px;
}
.jia p:nth-child(1) {
  width: 40%;
  margin-right: 80px;
}
.kong {
  margin-top: 80px;
}
.body2 {
  background: url('../../static/image/block.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 8px;
  margin: 80px auto 40px;
  padding-bottom: 74px;
  box-sizing: border-box;
}
.Wcolor {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
}
.biaoti {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  height: 60px;
  width: 90%;
  margin: 0 auto;
  background: url('../../static/image/block2.png');
  background-repeat: no-repeat;
